@import "/Users/jerry/Projects/react-toolbox-demo/src/theme.sass";
@import "~react-toolbox/lib/commons.scss";

html,
body,
.react-container,
.root,
.leaflet-container, {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0; }

.controlPane {}
.mapPane {
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0; }

.mapPane {
  z-index: $z-index-lower; }

.controlPane {
  z-index: $z-index-lower + 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  padding: 10px; }

.sidebar {
  box-shadow: 0 0 10px hsla(0, 0%, 0%, .5); }

.componentsDemo {
  background: $palette-pink-50;
  padding: 2rem;
  margin: 10rem; }

@media screen and (max-width: $layout-breakpoint-xs) {
  .componentsDemo {
    background: $palette-pink-50;
    padding: 2rem;
    margin: 0; } }



